import React from 'react';
import styles from './Modal.module.css';
import close from '../../images/close.png';

// Props ModalOn  handle 
const Modal = ({
  setSignupValid,
  setDeleteValid,
  setPwdValid,
  setForm,
  setNickname,

  setPasswordDelete,
  setPassword,

  setIsErrorDelete,
  setIsErrorPassword,
  setIsErrorNickname,
  setIsErrorSignup,
  modalName,
  nickname,
  isModalOn,
  handleModal,
  children,
}) => {
  // modal    input value reset plans
  // 1) nickname   : nickname( response  nickname) props  handleModal()   setNickname nickname  
  // 2)    :

  const handleModalReset = (modalname) => {
    if (modalname === 'nickname') {
      setNickname(nickname);
      setIsErrorNickname(false);
    } else if (modalname === 'password') {
      setPassword({
        newPassword: '',
        confirmPassword: '',
        currentPassword: '',
      });
      setPwdValid({
        isValid: false,
        newPasswordValid: null,
        confirmPasswordValid: null,
        currentPasswordValid: null,
      });
      setIsErrorPassword(false);
    } else if (modalname === 'delete') {
      setPasswordDelete('');
      setDeleteValid(null);
      setIsErrorDelete(false);
    } else if (modalname === 'signup') {
      setForm({
        email: '',
        password: '',
        passwordCheck: '',
        nickname: '',
      });
      setSignupValid({
        email: null,
        password: null,
        passwordCheck: null,
        nickname: null,
      });
      setIsErrorSignup(false);
    }
  };

  const handleClickOutside = (e) => {
    if (isModalOn && e.target.id === 'outside') {
      handleModal();
      handleModalReset(modalName);
    }
  };

  return (
    <div>
      <div
        id="outside"
        className={`${styles.dimmer} ${isModalOn && styles.isOn}`}
        onClick={handleClickOutside}
      >
        <div className={styles.box}>
          <img
            className={styles.close}
            src={close}
            alt="close"
            onClick={() => {
              handleModal();
              handleModalReset(modalName);
            }}
          />
          {/*     <Modal> </Modal> 
               children   */}
          {children}
          {/*  */}
        </div>
      </div>
    </div>
  );
};

export default Modal;
