import { React, useState } from 'react';
import axios from 'axios';
import styles from './ToDoTask.module.css';
import deleteIcon from '../../images/delete.png';
import checkboxIcon from '../../images/stop.png';
import checkboxDoneIcon from '../../images/checkbox.png';

const ToDoTask = ({
  token,
  task,
  isDone,
  editOn,
  cardId,
  taskId,
  modifyToDoCardData,
  deleteToDoCardData,
}) => {
  const [checked, setChecked] = useState(isDone);
  const [text, setText] = useState(task);

  const setInputText = (e) => {
    setText(e.target.value);
  };

  // task  ,  state  re-render,       
  const deleteTask = async () => {
    deleteToDoCardData(cardId, taskId);
    try {
      await axios.post(
        'https://todaying.cf/main/deleteTask',
        {
          taskId,
        },
        {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        },
        { withCredentials: true },
      );
    } catch (err) {
      if (err.response) {
        throw err;
      }
    }
  };
  // isDone   ,  cards state ,      
  const handleIsDone = async () => {
    const newIsDone = checked === '0' ? '1' : '0';
    modifyToDoCardData({ cardId, taskId, newTask: text, newIsDone });
    setChecked(newIsDone);

    try {
      await axios.post(
        'https://todaying.cf/main/updateTask',
        {
          cardId,
          taskId,
          task,
          isDone: newIsDone,
        },
        {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        },
        { withCredentials: true },
      );
    } catch (err) {
      if (err.response) {
        throw err;
      }
    }
  };

  //   task  ,  cards state ,      
  const handleTask = async (e) => {
    const newTask = e.target.value;
    modifyToDoCardData({ cardId, taskId, newTask, newIsDone: checked });

    try {
      await axios.post(
        'https://todaying.cf/main/updateTask',
        {
          cardId,
          taskId,
          task: newTask,
          isDone,
        },
        {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        },
      );
    } catch (err) {
      if (err.response) {
        throw err;
      }
    }
  };

  const checkEnterPress = (e) => {
    if (e.keyCode === 13) {
      e.target.blur();
    }
  };
  return (
    <div className={styles.task}>
      {checked === '1' ? (
        <img
          src={checkboxDoneIcon}
          alt="checkbox"
          className={styles.task_checkboxDone}
          onClick={handleIsDone}
        />
      ) : (
        <img
          src={checkboxIcon}
          alt="checkbox"
          className={styles.task_checkbox}
          onClick={handleIsDone}
        />
      )}

      <input
        onKeyDown={checkEnterPress}
        className={`${styles.task_text} ${checked === '1' && styles.isDone}`}
        value={text}
        spellCheck="false"
        onBlur={handleTask}
        onChange={setInputText}
      />

      {editOn ? (
        <img
          className={styles.task_delete}
          src={deleteIcon}
          alt="delete"
          onClick={deleteTask}
        />
      ) : null}
    </div>
  );
};

export default ToDoTask;
